













import Vue, { PropType } from 'vue';
import { IPerson } from '@/interfaces/models/person.model.interface';

export default Vue.extend({
  name: 'TimelineGridBody',
  props: {
    rows: Array,
    rowName: {
      type: Function as PropType<(person: IPerson) => string>,
      required: true,
    },
    rowId: {
      type: Function as PropType<(person: IPerson) => string>,
      required: true,
    },
  },
});
